
import { defineComponent } from 'vue'
import { dateFormatterCurrying } from '@/utils/data-transfer'
export default defineComponent({
  name: '',
  props: {
    Lists: {
      type: Object,
      default() {
        return null
      }
    }
  },
  setup() {
    const supplierType = (row: any, column: any, cellValue: string) => {
      switch (cellValue) {
        case '0':
          return '临时供应商'
        case '1':
          return '一般供应商'
        case '2':
          return '重点供应商'
        case '3':
          return '内部供应商'
        default:
          return '未知'
      }
    }

    const dateFormatter = dateFormatterCurrying('YYYY/MM/DD')

    return {
      supplierType,
      dateFormatter
    }
  }
})
